import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Card from "../components/Card";
// import Carousel from '../components/Carousel'
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import { nanoid } from "nanoid";
import Loader from "../components/Loader";
export default function Home() {
  const [foodCat, setFoodCat] = useState([]);
  const [normalData, setNormalData] = useState([]);
  const [foodItems, setFoodItems] = useState([]);
  const [search, setSearch] = useState("");
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(true)
  const navigate = useNavigate();

  if (!localStorage.getItem("token")) {
    navigate("/login");
  }
  async function getMydat(params) {
    let response = await fetch("https://techtastebackend.onrender.com/foodData", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    });
    response = await response.json();

    setFoodItems(response);
    setNormalData(response)
    setLoading(false)
  }

  useEffect(() => {

    getMydat();
  }, [window.location]);


  const handleSearch = (data) => {
    let filterdFoodItems;
    if (searchQuery !== '') {
      filterdFoodItems = foodItems.filter((el) => {

        return el.name.toLowerCase().match(searchQuery);
      })
      setFoodItems(filterdFoodItems)

    } else {
      filterdFoodItems = foodItems
      setFoodItems(filterdFoodItems)
    }


  }
  const clearFilters = async () => {
    setSearchQuery('')
    setFoodItems(normalData)
  }
  if (loading) {
    return <Loader />
  }

  // useEffect((e) => {

  // }, [])

  return (
    <div>
      <div>
        <Navbar />
      </div>
      <div>
        <div
          id="carouselExampleFade"
          className="carousel slide carousel-fade "
          data-bs-ride="carousel"
        >
          <div className="carousel-inner " id="carousel">
            <div class=" carousel-caption  " style={{ zIndex: "9" }}>
              <div className=" d-flex justify-content-center">
                {" "}
                {/* justify-content-center, copy this <form> from navbar for search box */}
                <input
                  className="form-control me-2 w-75 bg-white text-dark"
                  type="text"
                  placeholder="Search in here..."
                  aria-label="Search"
                  value={searchQuery}
                  onChange={(e) => {
                    handleSearch(e.target.value)
                    setSearchQuery(e.target.value)
                    // setSearch(e.target.value);
                  }}
                />
                <button
                  className="btn text-white bg-danger"
                  onClick={clearFilters}
                // onClick={() => myFun()}
                >
                  Clear Filters
                </button>
              </div>
            </div>
            <div className="carousel-item active">
              <img
                src="https://source.unsplash.com/random/900x700/?burger"
                className="d-block w-100  "
                style={{ filter: "brightness(30%)" }}
                alt="..."
              />
            </div>
            <div className="carousel-item">
              <img
                src="https://source.unsplash.com/random/900x700/?pastry"
                className="d-block w-100 "
                style={{ filter: "brightness(30%)" }}
                alt="..."
              />
            </div>
            <div className="carousel-item">
              <img
                src="https://source.unsplash.com/random/900x700/?barbeque"
                className="d-block w-100 "
                style={{ filter: "brightness(30%)" }}
                alt="..."
              />
            </div>
          </div>
          <button
            className="carousel-control-prev"
            type="button"
            data-bs-target="#carouselExampleFade"
            data-bs-slide="prev"
          >
            <span
              className="carousel-control-prev-icon"
              aria-hidden="true"
            ></span>
            <span className="visually-hidden">Previous</span>
          </button>
          <button
            className="carousel-control-next"
            type="button"
            data-bs-target="#carouselExampleFade"
            data-bs-slide="next"
          >
            <span
              className="carousel-control-next-icon"
              aria-hidden="true"
            ></span>
            <span className="visually-hidden">Next</span>
          </button>
        </div>
      </div>
      <div className="container">
        {" "}
        {/* boootstrap is mobile first */}
        <div className="container">
          <h1 className="mt-5">Food Products</h1>
          <hr />
          <div className="row">
            {foodItems &&
              foodItems.map((filterItems, index) => {
                return (
                  <>
                    <div
                      key={filterItems.id}
                      className="col-sm-12 col-md-6 col-lg-3"
                    >
                      <Card
                        foodName={filterItems.name}
                        item={filterItems}
                        options={filterItems.options}
                        ImgSrc={filterItems.img}
                        getMydat={getMydat}
                      ></Card>
                    </div>
                  </>
                );
              })}
          </div>
        </div>
        {foodCat !== []
          ? foodCat.map((data) => {
            return (
              // justify-content-center
              <div className="row mb-3">
                <div key={data.id} className="fs-3 m-3">
                  {data.CategoryName}
                </div>
                <hr
                  id="hr-success"
                  style={{
                    height: "4px",
                    backgroundImage:
                      "-webkit-linear-gradient(left,rgb(0, 255, 137),rgb(0, 0, 0))",
                  }}
                />
                {foodItems !== [] ? (
                  foodItems
                    .filter(
                      (items) =>
                        items.CategoryName === data.CategoryName &&
                        items.name
                          .toLowerCase()
                          .includes(search.toLowerCase())
                    )
                    .map((filterItems) => {
                      return (
                        <div
                          key={filterItems.id}
                          className="col-12 col-md-6 col-lg-3"
                        >
                          <Card
                            foodName={filterItems.name}
                            item={filterItems}
                            options={filterItems.options[0]}
                            ImgSrc={filterItems.img}
                          ></Card>
                        </div>
                      );
                    })
                ) : (
                  <div> No Such Data </div>
                )}
              </div>
            );
          })
          : ""}
      </div>
      <Footer />
    </div>
  );
}
